// 资金流入流出详情 - fanhualing

import { Component, OnInit, Input, Output, OnChanges, EventEmitter } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';
import { Message } from 'primeng/primeng';
import { DatePipe } from '@angular/common';
import { DomSanitizer } from '@angular/platform-browser';
import { TZB_HTTP_ASSET, TZB_HTTP_COMIF } from 'app/@uisftech/common/service/http.constant';
import * as moment from 'moment';

@Component({
  selector: 'asset-flow-direction-details',
  templateUrl: './asset-flow-direction-details.component.html',
  styleUrls: ['./asset-flow-direction-detailscomponent.scss'],
  providers: [Commfunc, DatePipe]
})
export class AssetFlowDirectionDetailsComponent implements OnInit {

  @Input() inValue: any;
  startDate: Date = null;
  endDate: Date = null;
  ch: any = CALENDAR_CN;
  msgs: Message[];
  user: any;
  userId: any;
  targetId: any;
  orgId: any;
  srcUrl_1;
  custType: string = '0';
  drCrFlag: string;

  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public datePipe: DatePipe,
    private sanitizer: DomSanitizer,
  ) {
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.userId = this.user.userId;
    this.targetId = this.user.targetId;
    this.orgId = this.user.orgId;
  }

  ngOnInit() {
    this.query(this.inValue.demogPeriod);
  }

  // 上月、本月、本周
  query(type?) {
    if (type == 'A') { // 上月
      let start = moment().subtract('month', 1).format('YYYY-MM') + '-01';
      let end = moment(start).subtract('month', -1).add('days', -1).format('YYYY-MM-DD');
      this.startDate = new Date(start);
      this.endDate = new Date(end);
    } else if (type == 'B') { // 本月
      let start = moment().add('month', 0).format('YYYY-MM') + '-01';
      let end = moment().format('YYYY-MM-DD');
      this.startDate = new Date(start);
      this.endDate = new Date(end);
    } else if (type == 'C') { // 本周
      let weekOfday = parseInt(moment().format('d')) // 今天是这周的第几天，周日为一周的第一天
      let start = moment().subtract(weekOfday, 'days').format('YYYY-MM-DD'); // 周一日期
      let end = moment().add(7 - weekOfday - 1, 'day').format('YYYY-MM-DD'); // 周六日期
      this.startDate = new Date(start);
      this.endDate = new Date(end);
    }
    this.queryData();
  }

  // 查询
  queryData() {
    if ((!this.startDate || this.startDate == null) && (!this.endDate || this.endDate == null)) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入查询的开始日期和结束时间' }];
      return;
    }
    if (this.startDate && this.startDate != null && (!this.endDate || this.endDate == null)) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入查询结束时间' }];
      return;
    } else if (this.endDate && this.endDate != null && (!this.startDate || this.startDate == null)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入查询起始时间' }];
      return;
    }
    // 借贷标志 D借 C贷
    if (this.custType == '1') {
      this.drCrFlag = 'C';
    } else if (this.custType == '2') {
      this.drCrFlag = 'D';
    } else {
      this.drCrFlag = undefined;
    }
    let startTime = this.commfunc.transDateN(this.startDate);
    let endTime = this.commfunc.transDateN(this.endDate);
    if (this.drCrFlag) {
      this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(TZB_HTTP_ASSET + '?userId=' + this.userId + '&&targetId=' + this.targetId + '&&orgId=' + this.orgId + '&&custNo=' + this.inValue.custNo + '&&endDate=' + endTime + '&&startDate=' + startTime + '&&drCrFlag=' + this.drCrFlag);
    } else {
      this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(TZB_HTTP_ASSET + '?userId=' + this.userId + '&&targetId=' + this.targetId + '&&orgId=' + this.orgId + '&&custNo=' + this.inValue.custNo + '&&endDate=' + endTime + '&&startDate=' + startTime);
    }
  }

  // 选择开始日期
  chectStart() {
    if (this.startDate && this.startDate != null && this.endDate && this.endDate != null) {
      let end_Time = moment(this.startDate).add(1, 'year').add(-1, 'days');
      let today_time = moment();
      if (end_Time > today_time) {
        this.endDate = new Date();
      } else {
        this.endDate = new Date(end_Time.format("Y-MM-DD"))
      }
      if (moment(this.startDate).format('YMMDD') > today_time.format('YMMDD')) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于当前日期' }];
        setTimeout(() => {
          this.startDate = new Date(today_time.format("Y-MM-DD"));
        });
        return;
      }
      if (this.startDate > this.endDate) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' }];
        setTimeout(() => {
          this.startDate = null;
        });
        return;
      }
    }
  }

  // 选择结束日期
  chectEnd() {
    if (this.endDate && this.endDate != null && this.startDate && this.startDate != null) {
      let this_time = moment();
      if (moment(this.endDate) > this_time) {
        this.endDate = new Date(this_time.format("Y-MM-DD"));
      }
      if (this.startDate.getTime() > this.endDate.getTime()) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' }];
        setTimeout(() => {
          this.endDate = null;
        });
        return;
      }
    }
  }
}
