import { Component, OnInit } from '@angular/core';
import {CusViewHttpService} from '../../../../http/custom-view/custom-view.http.service'; 
import {Commfunc} from 'app/@uisftech/common/service/commonFun'; 
import { tableMessage } from 'app/@uisftech/common/constant/config';    
// import { SubBussType, HeTongZhuangTai, FlagSign} from '../../../../constant/custView.codeValue';                                                                                       
@Component({
  selector: 'loan-all-details',
  templateUrl: './loan-all-details.component.html',
  styleUrls: ['./loan-all-details.component.scss'],
  providers:[CusViewHttpService,Commfunc]
})
export class LoanAllDetailsComponent implements OnInit {
  pageNum: number = 1;
  pageSize: number = 10;
  custNo:string;
  holdLoanInfototalNum:number;
  holdLoanInfopageSize:number;
  holdLoanInfopageNum:number;
  totalloanBalance: string;//贷款类总余额
  holdLoanInfo: any[]=[];//贷款
  tabMesg:any = tableMessage;//表格中无数据时显示的内容
  subBussTypeOptions;//子关系类型
  heTongZhuangTaiOptions;//合同状态
  flagSignOptions;//逾期标志
  holdLoanInfoToAll;
  code:any;
  constructor(
    public httpService:CusViewHttpService,
    public commfunc:Commfunc
  ) {
    this.codeVal();
    this.subBussTypeOptions =this.code['SubBussType'] ;
    this.heTongZhuangTaiOptions =this.code['HeTongZhuangTai'] ;
    this.flagSignOptions =this.code['FlagSign'] ;
    this.custNo=this.commfunc.getSessionData('custNo');
   }

  ngOnInit() {
     
    this.selectHoldLoanInfo();
  }
  selectHoldLoanInfo(){
    let param={
      custNo: this.custNo,
      pageNum: this.pageNum,
      pageSize: this.pageSize,
    }
    this.httpService.selectHoldLoanInfo(param).subscribe(data => {
      this.holdLoanInfopageNum=data.pageNum;
      this.holdLoanInfopageSize=data.pageSize;
      this.holdLoanInfototalNum=data.totalNum;
      this.totalloanBalance = data.totalloanBalance;
      this.holdLoanInfo = data.prodInfoList;//贷款
    });//贷款
  }
  paginate(event){
    this.pageSize=event.rows;
    let currentPage=event.page+1;
    this.pageNum=currentPage;
    this.selectHoldLoanInfo();
  }
  //码值
codeVal() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
}
}
