import { Component, OnInit, Input, Output, OnChanges, EventEmitter } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN, yearRangeBig, yearRangeSmall } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';
import { Message } from 'primeng/primeng';
import { DatePipe } from '@angular/common';
import { DomSanitizer } from '@angular/platform-browser';
import { TZB_HTTP_COMIF } from 'app/@uisftech/common/service/http.constant';
import * as moment from 'moment';
//账户交易明细
@Component({
  selector: 'account-trading-details',
  templateUrl: './account-trading-details.component.html',
  styleUrls: ['./account-trading-details.component.scss'],
  providers: [Commfunc, DatePipe]
})
export class AccountTradingDetailsComponent implements OnInit, OnChanges {
  @Input() inValue: any;
  @Output() outValue = new EventEmitter();
  items: any[];
  startDate: Date = null;
  endDate: Date = null;
  pageNum: number = 1;
  pageSize: number = 10;
  totalNum: number;
  holdDepositInfopageSize: number;
  holdDepositInfototalNum: number;
  holdDepositInfopageNum: number;
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  ch: any = CALENDAR_CN;//日期
  msgs: Message[];
  yearRangeSmall: any = yearRangeSmall;
  yearRangeBig: any = yearRangeBig;
  yearRange: any;
  user: any;
  userId: any;
  targetId: any;
  orgId: any;
  srcUrl_1;
  custType: string = '0';
  drCrFlag: string;
  juege: boolean = false;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public datePipe: DatePipe,
    private sanitizer: DomSanitizer
  ) {
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.userId = this.user.userId;
    this.targetId= this.user.targetId;
    this.orgId = this.user.orgId;
   }

  ngOnInit() {
    // let s = new Date();
    // this.endDate = s;
    // let ddate = moment().add(-1, 'year').add(1, 'days').format('Y-MM-DD');
    // this.startDate = new Date(ddate);
    this.queryInit();//默认查询
  }
  ngOnChanges() {
    this.yearRange = `${yearRangeSmall}:${yearRangeBig}`;
    if (this.inValue) {
    }
  }

  // 默认查询
  queryInit() {
    this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(TZB_HTTP_COMIF + '?userId=' + this.userId + '&&targetId=' + this.targetId + '&&orgId=' + this.orgId + '&&acctNo=' + this.inValue.account);
  }

  queryList(item?) {
    this.juege = true;
    if (item) {
      let s = new Date();
      this.endDate = s;
      let range = '-' + item;
      let ddate = moment().add(range, 'month').add(1, 'days').format('Y-MM-DD');
      this.startDate = new Date(ddate);
    }
    if (this.custType == '1') {
      this.drCrFlag = '1';
    } else if (this.custType == '2') {
      this.drCrFlag = '2';
    } else {
      this.drCrFlag = undefined;
    }
    if ((!this.startDate || this.startDate == null || this.startDate == null) && (!this.endDate || this.endDate == null || this.endDate == null)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入查询的开始日期和结束时间' }];
      this.juege = false;
      return;
    }
    if (this.startDate && this.startDate != null && (!this.endDate || this.endDate == null)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入查询结束时间' }];
      this.juege = false;
      return;
    } else if (this.endDate && this.endDate != null && (!this.startDate || this.startDate == null)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入查询起始时间' }];
      this.juege = false;
      return;
    }
    let startDate1;
    startDate1 = this.datePipe.transform(this.startDate, 'yyyyMMdd')
    let endDate = this.commfunc.transDate(this.endDate);
    if (this.drCrFlag) {
      this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(TZB_HTTP_COMIF + '?userId=' + this.userId + '&&targetId=' + this.targetId + '&&orgId=' + this.orgId + '&&acctNo=' + this.inValue.account + '&&endDate=' + endDate + '&&startDate=' + startDate1 + '&&drCrFlag=' + this.drCrFlag);
      setTimeout(() => {
        this.juege = false;
      }, 1000);
    } else {
      this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(TZB_HTTP_COMIF + '?userId=' + this.userId + '&&targetId=' + this.targetId + '&&orgId=' + this.orgId + '&&acctNo=' + this.inValue.account + '&&endDate=' + endDate + '&&startDate=' + startDate1);
      setTimeout(() => {
        this.juege = false;
      }, 1000);
    }
   
  }

  chectStart() {
    console.log(1)
    if (this.startDate && this.startDate != null && this.endDate && this.endDate != null) {
      let end_Time = moment(this.startDate).add(1, 'year').add(-1, 'days');
      let today_time = moment();
      if (end_Time > today_time) {
        this.endDate = new Date();
      } else {
        this.endDate = new Date(end_Time.format("Y-MM-DD"))
      }
      if (moment(this.startDate).format('YMMDD') > today_time.format('YMMDD')) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于当前日期' }];
        setTimeout(() => {
          this.startDate = new Date(today_time.format("Y-MM-DD"));
        });
        return;
      }
      if (this.startDate > this.endDate) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' }];
        setTimeout(() => {
          this.startDate = null;
        });
        return;
      }
      if (moment(this.startDate).add(1, 'year').add(-1, 'days').format('YMMDD') < moment(this.endDate).format('YMMDD')) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询结束时间与起始日期间隔不能超过一年' }];
        setTimeout(() => {
          this.startDate = null;
        });
        return;
      }
    }
  }
  chectEnd() {
    console.log(3)
    if (this.endDate && this.endDate != null && this.startDate && this.startDate != null) {
      let this_time = moment();
      if (moment(this.endDate) > this_time) {
        this.endDate = new Date(this_time.format("Y-MM-DD"));
      }
      if (this.startDate.getTime() > this.endDate.getTime()) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' }];
        setTimeout(() => {
          this.endDate = null;
        });
        return;
      }
      if (moment(this.startDate).add(1, 'year').add(-1, 'days').format('YMMDD') < moment(this.endDate).format('YMMDD')) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '查询结束时间与起始日期间隔不能超过一年' }];
        setTimeout(() => {
          this.endDate = null;
        });
        return;
      }
    }
  }

  // 手动输入日期
  handInputDate(param1, param2) {
    this.showTime(param1, param2);
  }

  // 开始、结束日期判断
  showTime(param1, param2) {
    if (param1 == 'plan') {
      if (this.startDate && this.endDate) {
        if (this.startDate > this.endDate) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' });
          setTimeout(() => {
            if (param2 == "start") {
              this.startDate = null;
            } else {
              this.endDate = null;
            }
          })
        }
        if (moment(this.startDate).add(1, 'year').add(-1, 'days').format('YMMDD') < moment(this.endDate).format('YMMDD')) {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '查询结束时间与起始日期间隔不能超过一年' }];
          setTimeout(() => {
            if (param2 == "start") {
              this.startDate = null;
            } else {
              this.endDate = null;
            }
          });
        }
      }
    }
  }
}
