import { Component, OnInit, OnChanges } from '@angular/core';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { Observable } from 'rxjs/Rx';
import {Commfunc} from 'app/@uisftech/common/service/commonFun';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
// import { STATUS_Id } from '../../../constant/custView.codeValue';
import {Message} from 'primeng/primeng';
@Component({
  selector: 'app-recommended-product',
  templateUrl: './recommended-product.component.html',
  styleUrls: ['./recommended-product.component.scss'],
	providers: [Commfunc]
})
export class RecommendedProductComponent implements OnInit, OnChanges {
  custNo:any;
  custRecomProdInfo:any;
  pageSize:number=10;
  pageNum:number=1;
  totalNum:number=0;
  first:number=0;
  tabMesg:any = tableMessage;//表格中无数据时显示的内容
  ch: any = CALENDAR_CN;//日期
  historyCustRecomendProduct:any;
  historypageNum:number=1;
  historypageSize:number=10;
  historytotalNum:number;
  statusIdOptions:any[]=[];//推荐产品推荐状态
  statusId:any='';
  startDtA:any;//推荐起始日
  endDtA:any;//推荐起始结束日
  startDtB:any;//推荐截止日
  endDtB:any;//推荐截止结束日
  msgs:Message[];
  code:any;
  constructor(
    public router: Router,
    public httpService: CusViewHttpService,
    public route: ActivatedRoute,
    public commfunc:Commfunc
  ) { 
    this.codeVal();
    this.statusIdOptions=this.code['STATUS_Id'];
    this.custNo=this.commfunc.getSessionData('custNo');
    
  }

  ngOnInit() {
    this.selectCustRecomProdInfo();
    this.selectPartyCommendProdInfo();
  }
  ngOnChanges(){
    
  }

  selectCustRecomProdInfo(){
    let param={
      // custNo:'1300002112',
      custNo:this.custNo,
      pageSize:this.pageSize,
      pageNum:this.pageNum
    }
    this.httpService.selectCustRecomProdInfo(param).subscribe(data=>{
      if(data.returnCode.code=='success'){
        this.custRecomProdInfo=data.infoList;
        this.pageSize=data.pageSize;
        this.pageNum=data.pageNum;
        this.totalNum=data.totalNum;
      }
    })
  }
  paginate(event){
    this.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    this.selectCustRecomProdInfo();
  }

  selectPartyCommendProdInfo(){
    let param={
      custNo:this.custNo,
      pageNum:this.historypageNum,
      pageSize:this.historypageSize,
      statusId:this.statusId,
      startDtA:this.startDtA,
      endDtA:this.endDtA,
      startDtB:this.startDtB,
      endDtB:this.endDtB,
    }
    this.httpService.selectPartyCommendProdInfo(param).subscribe(data=>{
      if(data.returnCode.code=='success'){
        this.historyCustRecomendProduct=data.infoList;
      }else{
        // this.msgs = [];
        // this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    });
  }
  paginateS(event){
    this.historypageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.historypageNum = currentPage;
    this.selectPartyCommendProdInfo();
  }
  reset(){
    this.statusId='';
    this.startDtA=null;
    this.endDtA=null;
    this.startDtB=null;
    this.endDtB=null;
  }
       //权限
       judgeField(fId) {
        
            let ret = this.commfunc.permissionFiledCheck(fId);
            return ret
          }
          toOwnOurProduct() {
            this.router.navigate(['pages/custom-view/view-product-information/own-our-product'])
          }
          toApplForProduct() {
            this.router.navigate(['pages/custom-view/view-product-information/appl-for-product'])
          }
          toOtherBankProduct() {
            this.router.navigate(['pages/custom-view/view-product-information/other-bank-product'])
          }
          toRecommendedProduct() {
            this.router.navigate(['pages/custom-view/view-product-information/recommended-product'])
          }
        
          tock() {
            if (document.querySelector('#ck') != null) {
              document.querySelector('#ck').scrollIntoView();
              let s=window.scrollY;
              window.scrollTo(100,s-50);
            }
          }
          toDk() {
            if (document.querySelector('#dk') != null) {
              document.querySelector('#dk').scrollIntoView();
              let s=window.scrollY;
              window.scrollTo(100,s-50);
            }
          }
          toLc() {
            if (document.querySelector('#lc') != null) {
              document.querySelector('#lc').scrollIntoView();
              let s=window.scrollY;
              window.scrollTo(100,s-50);
            }
          }
          toOtherQy() {
            if (document.querySelector('#otherqy') != null) {
              document.querySelector('#otherqy').scrollIntoView();
              let s=window.scrollY;
              window.scrollTo(100,s-50);
            }
          }
          toDj() {
            if (document.querySelector('#dj') != null) {
              document.querySelector('#dj').scrollIntoView();
              let s=window.scrollY;
              window.scrollTo(100,s-50);
            }
          }
          toMy() {
            if (document.querySelector('#my') != null) {
              document.querySelector('#my').scrollIntoView();
              let s=window.scrollY;
              window.scrollTo(100,s-50);
            }
          }
          //码值
            codeVal() {
              this.commfunc.codeValue();
              let ret = this.commfunc.codeDatas;
              return this.code= ret //code是自己声明的变量
            }
}
