import { Component, OnInit, Output, Input, EventEmitter, OnChanges } from '@angular/core';
import { OtherBankingProduct } from '../../../bean/custom-view.bean';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
// import { CURRENCY } from '../../../../constant/custView.codeValue';
import * as _ from 'lodash';
import { tableMessage, CALENDAR_CN, yearRangeBig, yearRangeSmall } from 'app/@uisftech/common/constant/config';
import { checkNumber, checkTel, sixthTwoNum } from 'app/@uisftech/common/service/validator';
@Component({
  selector: 'app-add-other-loan',
  templateUrl: './add-other-loan.component.html',
  styleUrls: ['./add-other-loan.component.scss'],
  providers: [Commfunc]
})
export class AddOtherLoanComponent implements OnInit, OnChanges {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';
  custNo: string;
  otherBankingProduct: OtherBankingProduct = new OtherBankingProduct();
  distributeCustQryForm: FormGroup;//校验 
  msgs: Message[] = [];
  currencyOptions: any[] = [];//币种
  ch: any = CALENDAR_CN;//日期
  checkNumber = checkNumber;//校验数字
  today: Date = new Date();
  yearRangeSmall: any = yearRangeSmall;
  yearRangeBig: any = yearRangeBig;
  yearRange: any;
  code: any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder
  ) {
    this.codeVal();
    this.currencyOptions = this.code['CURRENCY'];
    this.custNo = this.commfunc.getSessionData('custNo');
  }

  ngOnInit() {
    this.otherBankingProduct.currCd = '1';
    this.otherBankingProduct.regDt = this.tranToday(this.today.toISOString());
  }
  ngOnChanges() {
    this.yearRange = `${yearRangeSmall}:${yearRangeBig}`;
    // this.commfunc.transDate(param.regDt)
    if (this.inValue) {
      if (this.inValue.indProdCd == '10') {
        this.distributeCustQryForm = this.fb.group({
          'custNo': new FormControl('', ),//	客户号
          'custName': new FormControl('', ),//	客户姓名
          'avgBal': new FormControl('', Validators.required),//	日均（最近6个月平均使用金额）
          // 'indProdCd': new FormControl('', Validators.required),//	他行产品标志
          'currCd': new FormControl('', Validators.required),//	币种
          // 'loanAmt': new FormControl('',  Validators.required),//	金额（贷款金额）
          'loanBal': new FormControl('', Validators.required),//	余额（贷款余额）
          'remark': new FormControl('', ),//	备注
          'regDt': new FormControl('', ),//	登记时间
          'dataSrc': new FormControl('', ),//	数据来源
          'bankName': new FormControl('', Validators.required),//银行名称          
        })
      } else if (this.inValue.indProdCd == '20') {
        this.distributeCustQryForm = this.fb.group({
          'custNo': new FormControl('', ),//	客户号
          'custName': new FormControl('', ),//	客户姓名
          'avgBal': new FormControl('', Validators.required),//	日均（最近6个月平均使用金额）
          // 'indProdCd': new FormControl('', Validators.required),//	他行产品标志
          'currCd': new FormControl('', Validators.required),//	币种
          // 'loanAmt': new FormControl('',  Validators.required),//	金额（贷款金额）
          'loanBal': new FormControl('', Validators.required),//	余额（贷款余额）
          'remark': new FormControl('', ),//	备注
          'regDt': new FormControl('', ),//	登记时间
          'dataSrc': new FormControl('', ),//	数据来源
          'bankName': new FormControl('', Validators.required),//银行名称          
        })
      } else if (this.inValue.indProdCd == '30') {
        this.distributeCustQryForm = this.fb.group({
          'custNo': new FormControl('', ),//	客户号
          'custName': new FormControl('', ),//	客户姓名
          'avgBal': new FormControl('', Validators.required),//	日均（最近6个月平均使用金额）
          // 'indProdCd': new FormControl('', Validators.required),//	他行产品标志
          'currCd': new FormControl('', Validators.required),//	币种
          'loanAmt': new FormControl('', Validators.required),//	金额（贷款金额）
          'loanBal': new FormControl('', Validators.required),//	余额（贷款余额）
          'remark': new FormControl('', ),//	备注
          'regDt': new FormControl('', ),//	登记时间
          'dataSrc': new FormControl('', ),//	数据来源
          'bankName': new FormControl('', Validators.required),//银行名称
        })
      } else if (this.inValue.indProdCd == '40') {
        this.distributeCustQryForm = this.fb.group({
          'custNo': new FormControl('', ),//	客户号
          'custName': new FormControl('', ),//	客户姓名
          'avgBal': new FormControl('', Validators.required),//	日均（最近6个月平均使用金额）
          // 'indProdCd': new FormControl('', Validators.required),//	他行产品标志
          'currCd': new FormControl('', Validators.required),//	币种
          'loanAmt': new FormControl('', Validators.required),//	金额（贷款金额）
          // 'loanBal': new FormControl('', Validators.required ),//	余额（贷款余额）
          'remark': new FormControl('', ),//	备注
          'regDt': new FormControl('', ),//	登记时间
          'dataSrc': new FormControl('', ),//	数据来源
          'bankName': new FormControl('', Validators.required),//银行名称          
        })
      }
      this.otherBankingProduct = new OtherBankingProduct();
      this.otherBankingProduct.custNo = this.custNo;
      if (this.otherBankingProduct.custNo && this.otherBankingProduct.custNo != '') {
        this.httpService.selectCustBriefInfo({ custNo: this.custNo }).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.otherBankingProduct.custNo = data.custNo;
            this.otherBankingProduct.custName = data.custName;
          }
        })
      }
    }
  }
  submit() {
    //校验的
    for (const i in this.distributeCustQryForm.controls) {
      this.distributeCustQryForm.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.distributeCustQryForm.controls) {
      if (!this.distributeCustQryForm.controls[i].valid) {
        return;
      }
    }
    if (this.otherBankingProduct.dataSrc == 'BD') {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '数据来源不能为BD!' }];
      return;
    }
    // this.otherBankingProduct.dataSrc='手工录入';
    if (!this.otherBankingProduct.loanAmt || this.otherBankingProduct.loanAmt == '' || this.otherBankingProduct.loanAmt == null) {
      this.otherBankingProduct.loanAmt = '0';
    }
    if (!this.otherBankingProduct.avgBal || this.otherBankingProduct.avgBal == '' || this.otherBankingProduct.avgBal == null) {
      this.otherBankingProduct.avgBal = '0';
    }
    if (!this.otherBankingProduct.loanBal || this.otherBankingProduct.loanBal == '' || this.otherBankingProduct.loanBal == null) {
      this.otherBankingProduct.loanBal = '0';
    }
    let param = _.clone(this.otherBankingProduct);
    param.regDt = this.commfunc.transDate(new Date(param.regDt));
    param.indProdCd = this.inValue.indProdCd;
    if (param.indProdCd == '30') {
      if (param.loanAmt && param.loanBal && param.loanAmt < param.loanBal) {
        this.msgs = [{ severity: 'error', summary: '提示', detail: '所输贷款余额不能大于贷款金额' }];
        return;
      }
    }
    this.httpService.addCustOtherProdInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
        this.otherBankingProduct = new OtherBankingProduct();
        this.outValue.emit('新增他行贷款');
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    })
  }
  close() {
    this.outValue.emit('取消');
  }

  onKeyupNumber(event) {
    if (checkNumber(null, event.target.value)) {
      // this.msgs = [{ severity: 'error', summary: '提示', detail: '手机号码输入有误' }];
      event.target.value = '';
    }
  }
  //16位整数或16位整数保留两位小数
  onkeyupSixtwoNum(event) {
    if (sixthTwoNum(null, event.target.value)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '所输金额必须为0-16位正整数或0-16位正整数保留两位小数' }];
      event.target.value = '';
    }
  }

  tranToday(value) {
    if (value && value.length == 8) {
      let a = value.slice(0, 4);
      let b = value.slice(4, 6);
      let c = value.slice(6, 8);
      return a + '-' + b + '-' + c;
    } else if (value && value.length >= 10) {
      value = value.slice(0, 10)
      return value;
    } else {
      return "";
    }
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
