  ngOnInit() {
    this.ulData = [
      {
        label: '持有我行产品', processCode: 'X_CUST_M_P007_P129',
        children: [
          { label: '存款', processCode: 'X_CUST_M_P007_P129_P227', typeNum: '1' },
          { label: '贷款', processCode: 'X_CUST_M_P007_P129_P228', typeNum: '2' },
          { label: '理财', processCode: 'X_CUST_M_P007_P129_P229', typeNum: '3' },
          { label: '签约类', processCode: 'X_CUST_M_P007_P129_P230', typeNum: '4' },
          { label: '信用卡', processCode: 'X_CUST_M_P007_P129_P231', typeNum: '5' },
          { label: '国际业务', processCode: 'X_CUST_M_P007_P129_P232', typeNum: '6' },
          { label: '承兑', processCode: 'X_CUST_M_P007_P129_P228_P313', typeNum: '7' },
          { label: '贴现', processCode: 'X_CUST_M_P007_P129_P228_P312', typeNum: '8' },
          { label: '保函', processCode: 'X_CUST_M_P007_P129_P228_P314', typeNum: '9' }]
      },
      { label: '正在申请产品', processCode: 'X_CUST_M_P007_P130', typeNum: '10' },
      { label: '他行产品', processCode: 'X_CUST_M_P007_P131', typeNum: '11' },
      { label: '推荐产品', processCode: 'X_CUST_M_P007_P132', typeNum: '12' },
    ]
    this.qryRelationItemsPageNum = 1;
    this.qryRelationItemsPageSize = 10;
    this.queryVoucher();//抵用券查询
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (!this.custName) {
      this.selectCustName();
    }

    let this_item = {
      clientFlag: '0',
      clientNo: this.custNo,
    }
    this.httpService.BZ_0200300000101_LOAN(this_item).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.totalloanBalance = data.loanBalance;
      }
    });//贷款总余额


    // 存款
    let param = {
      custNo: this.custNo,
      pageNum: this.holdDepositInfopageNum,
      pageSize: this.holdDepositInfopageSize,
    }
    this.httpService.selectCustAsLnChangeInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        for (let item of data.infoList) {
          if (item.prodBal == null) {
            item.prodBal = '-';
          } else {
            item.prodBal = (item.prodBal / 1).toFixed(2);
          }
          if (item.lastBal == null) {
            item.lastBal = '-';
          } else {
            item.lastBal = (item.lastBal / 1).toFixed(2);
          }
          if (item.monthEndBal == null) {
            item.monthEndBal = '-';
          } else {
            item.monthEndBal = (item.monthEndBal / 1).toFixed(2);
          }
          if (item.yearBeginBal == null) {
            item.yearBeginBal = '-';
          } else {
            item.yearBeginBal = (item.yearBeginBal / 1).toFixed(2);
          }
        }
        data.infoList.forEach(item => {
          if (item.prodType == '11') {
            this.salesBean.depositBal = item.prodBal;
            this.salesBean.depositBalCLast = item.lastBal;
            this.salesBean.depositBalCMonthEnd = item.monthEndBal;
            this.salesBean.depositBalCYearBegin = item.yearBeginBal;
          } else if (item.prodType == '12') {
            this.salesBean.financingBal = item.prodBal;
            this.salesBean.financingBalCLast = item.lastBal;
            this.salesBean.financingBalCMonthEnd = item.monthEndBal;
            this.salesBean.financingBalCYearBegin = item.yearBeginBal;
          } else if (item.prodType == '21') {
            this.salesBean.loanBal = item.prodBal;
            this.salesBean.loanBalCLast = item.lastBal;
            this.salesBean.loanBalCMonthEnd = item.monthEndBal;
            this.salesBean.loanBalCYearBegin = item.yearBeginBal;
          } else if (item.prodType == '22') {
            this.salesBean.acceptanceBal = item.prodBal;
            this.salesBean.acceptanceBalCLast = item.lastBal;
            this.salesBean.acceptanceBalCMonthEnd = item.monthEndBal;
            this.salesBean.acceptanceBalCYearBegin = item.yearBeginBal;
          } else if (item.prodType == '24') {
            this.salesBean.guaranteeBal = item.prodBal;
            this.salesBean.guaranteeBalCLast = item.lastBal;
            this.salesBean.guaranteeBalCMonthEnd = item.monthEndBal;
            this.salesBean.guaranteeBalCYearBegin = item.yearBeginBal;
          } else if (item.prodType == '25') {
            this.salesBean.creditCardBal = item.prodBal;
            this.salesBean.creditCardBalCLast = item.lastBal;
            this.salesBean.creditCardBalCMonthEnd = item.monthEndBal;
            this.salesBean.creditCardBalCYearBegin = item.yearBeginBal;
          }
        });
        this.totalBalance = data.totalBalance;
        this.totalAvailableCardinality = data.totalAvailableCardinality;
        this.totalYearAdditionalCardinality = data.totalYearDailyAverage;//本年日均
        this.totalContributionCardinalNumber = data.totalMonthDailyAverage;//本月日均
        // this.holdDepositInfopageNum = data.pageNum;
        // this.holdDepositInfopageSize = data.pageSize;
        this.holdDepositInfototalNum = data.totalNum;
        this.holdDepositInfo = data.prodInfoList == null ? [] : data.prodInfoList;//存款 
        if (!this.holdDepositInfo || this.holdDepositInfo.length == 0) {
          this.holdDepositInfoClass = true;
        } else {
          this.holdDepositInfoChange();
        }
        this.showCircle(this.holdDepositInfo, '1');
      }

      setTimeout(() => {

        this.selectHoldLoanInit(); // 贷款台账

        this.BZ_1100300000709_NEW_CXInit(); // 贷款账号

        this.queryCreditContractInit(); // 授信合同

        this.queryMaximumContractInit(); // 最高额合同

        this.selectHoldForfaitingInfoTxInit(); // 贴现

        this.BZ_0300300004417_LOANInit(); //承兑合同

        this.selectHoldBackletterInfoInit(); //保函

        this.selectHoldMoneyManagementInfoInit(); // 理财

        this.holdBankCardInit(); // 信用卡

        this.selectHoldAgrmtInfoInit();// 签约服务

        this.holdForfaitingInit(); // 福费廷

        this.selectHoldTradeFinancingInfoInit(); // 贸易融资

        this.selectHoldCktxInfoInit(); // 出口贴现

        this.holdCreditLetterInfoInit(); // 信用证

      }, 1000);
    });

    this.showHideBrief();
    this.showEcharts('B');
  }

