  // ------ 存款 -------
  selectCustAsLnChangeInit() {
    this.holdDepositInfoFirst = 0;
    this.holdDepositInfopageNum = 1;
    this.holdDepositInfopageSize = 10;
    this.selectCustAsLnChange()
  }
  selectCustAsLnChange() {
    let param = {
      custNo: this.custNo,
      pageNum: this.holdDepositInfopageNum,
      pageSize: this.holdDepositInfopageSize
    }
    this.httpService.selectCustAsLnChangeInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.glckBool = false;
        this.totalBalance = data.totalBalance;
        this.totalAvailableCardinality = data.totalAvailableCardinality;
        this.totalYearAdditionalCardinality = data.totalYearDailyAverage;//本年日均
        this.totalContributionCardinalNumber = data.totalMonthDailyAverage;//本月日均
        // this.holdDepositInfopageNum = data.pageNum;
        // this.holdDepositInfopageSize = data.pageSize;
        this.holdDepositInfototalNum = data.totalNum;
        this.holdDepositInfo = data.prodInfoList == null ? [] : data.prodInfoList;//存款
        this.holdDepositInfoChange();
      } else {
        this.glckBool = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.glckBool = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    });//存款
  }
  paginateHoldDepositInfo(event) {
    if (!this.glckBool) {
      this.glckBool = true;
      // 分页
      this.holdDepositInfopageSize = event.rows * 1;
    let currentPage = event.page + 1;
      this.holdDepositInfopageNum = currentPage;
      this.holdDepositInfoFirst = (currentPage - 1) * this.holdDepositInfopageSize;
      this.selectCustAsLnChange();
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '正在加载，请勿频繁操作！' });
    }
  }

