  //转码
  codeValue(val, arr: any = []) {
    let temp = "";
    if (arr.length > 0) {
      arr.forEach(item => {
        if (item['value'] == val) {
          temp = item['label']
        }
      });
    }
    return temp
  }
  //保留两位小数
  toTwoNum(data) {
    data = data.toFixed(2);
    return data;
  }
  //转换日期
  tranDate(value) {
    if (value && value.length == 8) {
      let a = value.slice(0, 4);
      let b = value.slice(4, 6);
      let c = value.slice(6, 8);
      return a + '-' + b + '-' + c;
    } else if (value && value.length >= 10) {
      value = value.slice(0, 10)
      return value;
    } else {
      return "";
    }
  }

  showDialogCd(data) {
    this.displayCd = true;
    document.body.style.overflow = 'hidden';
    this.inValue = data;
  }
