  // 资金流动方向
  showEcharts(demogPeriod) {
    this.arrLow = [];
    let flowInfo = {
      custNo: this.custNo, // 客户号
      demogPeriod: demogPeriod, // 统计周期
    }
    let thiss = this;
    this.httpService.selectAssertFlowInfo(flowInfo).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.amtNetting = null;
        this.amtNettingIn = null;
        this.amtNettingOut = null;
        let amtOutList = [], amtOutNameList = [];
        let amtInList = [], amtInNameList = [];
        this.arrLiuRu = [];
        this.arrLiuChu = [];
        // 根据push规则,先计算流出,后计算流入,顺序不可更改
        // 流出
        let outMap = data.outMap;
        if (outMap) {
          this.amtNettingOut = outMap.amtOutAll;
          amtOutList.push(
            outMap.amtOutA == null ? 0 : outMap.amtOutA,
            outMap.amtOutB == null ? 0 : outMap.amtOutB,
            outMap.amtOutC == null ? 0 : outMap.amtOutC
          )
          amtOutNameList.push(
            outMap.amtOutANm == null ? '' : outMap.amtOutANm,
            outMap.amtOutBNm == null ? '' : outMap.amtOutBNm,
            outMap.amtOutCNm == null ? '' : outMap.amtOutCNm,
          )
          this.sort(amtOutList, amtOutNameList, 'out');
        }
        // 流入
        let inMap = data.inMap;
        if (inMap) {
          // 资金净流入
          this.amtNetting = inMap.amtNetting;
          this.amtNettingIn = inMap.amtInAll;
          amtInList.push(
            inMap.amtInA == null ? 0 : inMap.amtInA,
            inMap.amtInB == null ? 0 : inMap.amtInB,
            inMap.amtInC == null ? 0 : inMap.amtInC
          );
          amtInNameList.push(
            inMap.amtInANm == null ? '' : inMap.amtInANm,
            inMap.amtInBNm == null ? '' : inMap.amtInBNm,
            inMap.amtInCNm == null ? '' : inMap.amtInCNm
          )
          this.sort(amtInList, amtInNameList, 'in');
        }

        this.assetOption = {
          title: {
            textStyle: {
              fontSize: '14',
              color: '#333',
            },
          },
          tooltip: {
            trigger: 'axis',
            backgroundColor: '#34CFE6',
            formatter: function (params) {
              let nameIn = params[0].axisValueLabel; // 交易对手名称
              let typeIn = params[0].seriesName; // 流入 or 流出
              let numIn = params[0].value;//金额
              let nameOut = params[1].axisValueLabel; // 交易对手名称
              let typeOut = params[1].seriesName; // 流入 or 流出
              let numOut = params[1].value;//金额
              if (params[0].seriesName == '流入' && params[0].value == 0) {//流出
                var result = `${nameOut}<br/>`;
                result += params[1].marker + `${typeOut}:${numOut}元`
                return result
              } else {//流入
                var result = `${nameIn}<br/>`;
                result += params[0].marker + `${typeIn}:${numIn}元`
                return result
              }
            }
          },
          legend: {
            data: ['流入', '流出'],
            top: '80%',
          },
          xAxis: {
            type: "value",
          },
          yAxis: {
            type: "category",
            data: this.arrLow,
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            axisLabel: {
              show: true,
              interval: 0,
              formatter: function (value) {
                return (value.length > 4 ? (value.slice(0, 4) + '...') : value)
              }
            },
            triggerEvent: true,
          },
          grid: {
            left: 90,
            top: 0,
          },
          series: [
            {
              name: "流入",
              type: "bar",
              stack: '总量',
              label: {
                normal: {
                  show: false,
                  color: '#fff',
                  formatter: function (params) {
                    if (params.value > 0 || params.value < 0) {
                      return params.value;
                    } else {
                      return '';
                    }
                  }
                }
              },
              itemStyle: {
                normal: {
                  color: '#e93f33',
                }
              },
              data: this.arrLiuRu,
            },
            {
              name: "流出",
              type: "bar",
              stack: '总量',
              label: {
                normal: {
                  show: false,
                  color: '#fff',
                  formatter: function (params) {
                    if (params.value > 0 || params.value < 0) {
                      return params.value;
                    } else {
                      return '';
                    }
                  }
                }
              },
              itemStyle: {
                normal: {
                  color: '#5bb01a',
                }
              },
              data: this.arrLiuChu,
            },
          ],
        }
      }
    });
  }

  // 排序
  sort(amtList, amtNameList, type) {
    let amtSortList = [], amtSortNameList = [];
    let amtInMin = amtList[0], amtInMax = amtList[0], min = 0, max = 0;
    let a = [0, 1, 2], mid;
    for (var i = 1; i < amtList.length; i++) {
      if (amtList[i] < amtInMin) {
        min = i;
        amtInMin = amtList[i];
      }
      if (amtList[i] > amtInMax) {
        max = i;
        amtInMax = amtList[i];
      }
    }
    if (min != max) {
      a.forEach(element => {
        if (element != min && element != max) {
          mid = element;
        }
      });
      amtSortList.push(amtList[min], amtList[mid], amtList[max]);
      this.arrLow.push(amtNameList[min], amtNameList[mid], amtNameList[max])
    } else {
      amtSortList = _.clone(amtList);
      this.arrLow.push(amtNameList[0], amtNameList[1], amtNameList[2])
    }
    if (type == 'in') {
      // 流入
      this.arrLiuRu = [];
      this.arrLiuRu.push(0, 0, 0, amtSortList[0], amtSortList[1], amtSortList[2]);
    } else {
      this.arrLiuChu = [];
      this.arrLiuChu.push(amtSortList[0], amtSortList[1], amtSortList[2], 0, 0, 0);
    }
  }

