  //排序
  doSort(event) {
    let column = '';
    let order = '';
    if (event.field == 'productStartDate') {
      column = 'raiseStartDt';
    } else if (event.field == 'productEndDate') {
      column = 'raiseEndDt';
    } else if (event.field == 'startDate') {
      column = 'startDt';
    } else if (event.field == 'endDate') {
      column = 'dueDt';
    } else if (event.field == 'subscriptionAmount') {
      column = 'finaAmt';
    } else if (event.field == 'expectedYield') {
      column = 'incomeRate';
    } else if (event.field == 'issue') {
      column = 'term';
    } else {
      column = undefined;
    }
    if (event.order == -1) {
      order = '1';
    } else if (event.order == 1) {
      order = '0';
    } else {
      order = undefined;
    }
    this.holdMoneyManagementInfopageColumn = column;
    this.holdMoneyManagementInfopageOrder = order;
    this.selectHoldMoneyManagementInfoInit();
  }

  // ------- 理财 ---------
  selectHoldMoneyManagementInfoInit() {
    this.holdMoneyManagementInfoFirst = 0;
    this.holdMoneyManagementInfopageSize = 10;
    this.holdMoneyManagementInfopageNum = 1;
    this.selectHoldMoneyInfo()
  }
  selectHoldMoneyInfo() {
    let columnNameList = [];
    if (this.holdMoneyManagementInfopageColumn && this.holdMoneyManagementInfopageOrder) {
      columnNameList.push({ column: this.holdMoneyManagementInfopageColumn, order: this.holdMoneyManagementInfopageOrder })
    }
    let param = {
      custNo: this.custNo,
      pageSize: this.holdMoneyManagementInfopageSize,
      pageNum: this.holdMoneyManagementInfopageNum,
      columnNameList: columnNameList,
    }
    this.httpService.selectHoldMoneyManagementInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.prodInfoList.forEach(item => {
          this.httpService.queryTellerById({ tellerId: item['mainContact'] }).subscribe(data => {
            if (data.tellerName && data.tellerName != null) {
              item['mainContact'] = data.tellerName;
            }
          });
          this.httpService.queryOrgById({ orgId: item.mainContactOrg }).subscribe(data => {
            if (data.orgName != null) {
              item.mainContactOrg = data.orgName;
            }
          });
          this.httpService.queryOrgById({ orgId: item.openAccountOrg }).subscribe(data => {
            if (data.orgName != null) {
              item.openAccountOrg = data.orgName;
            }
          });
          this.httpService.queryOrgById({ orgId: item.orgId }).subscribe(data => {
            if (data.orgName != null) {
              item.orgId = data.orgName;
            }
          });
          item['cardNum'] = item.cardDecimal;
        });
        this.holdMoneyManagementInfo = data.prodInfoList == null ? [] : data.prodInfoList;//理财
        if (!this.holdMoneyManagementInfo.length || this.holdMoneyManagementInfo.length == 0) {
          this.holdMoneyManagementInfoClass = true;
        }
        this.showCircle(this.holdMoneyManagementInfo, '3');
        this.holdMoneyManagementInfoChange();
        this.holdMoneyManagementInfototalNum = data.totalNum;
      }
    });
  }

