  childLiClick(parentIndex, childIndex, typeNum, type?) {
    if (!this.ulData[parentIndex].children[childIndex].isGroup) {
      this.ulData[parentIndex].children[childIndex].active = true;
      this.ulData[parentIndex].children.forEach((element, i) => {
        if (childIndex != i) {
          this.ulData[parentIndex].children[i].active = false;
        }
      });
      this.towhichPage(typeNum, type)
    }
  }

