import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import {CusViewHttpService} from '../../../../http/custom-view/custom-view.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'money-managerment-details',
  templateUrl: './money-managerment-details.component.html',
  styleUrls: ['./money-managerment-details.component.scss'],
  providers:[CusViewHttpService,Commfunc]

})
export class MoneyManagermentDetailsComponent implements OnInit {
  custNo:string;
  pageSize:number=10;
  pageNum:number=1;
  holdMoneyManagementInfo:any[];
  holdMoneyManagementInfopageNum:number;
  holdMoneyManagementInfopageSize:number;
  holdMoneyManagementInfototalNum:number;
  tabMesg:any = tableMessage;//表格中无数据时显示的内容
  constructor(
    public httpService:CusViewHttpService,
    public commfunc:Commfunc
  ) { 
    this.custNo=this.commfunc.getSessionData('custNo');
  }

  ngOnInit() {
    this.selectHoldMoneyManagementInfo();
  }
  selectHoldMoneyManagementInfo(){
    let param={
      custNo:this.custNo,
      pageSize:this.pageSize,
      pageNum:this.pageNum
    }
    this.httpService.selectHoldMoneyManagementInfo(param).subscribe(data=>{
      this.holdMoneyManagementInfopageNum=data.pageNum;
      this.holdMoneyManagementInfopageSize=data.pageSize;
      this.holdMoneyManagementInfototalNum=data.totalNum;
      this.holdMoneyManagementInfo=data.prodInfoList;
    });
  }
  paginate(event){
    this.pageSize=event.rows;
    let currentPage=event.page+1;
    this.pageNum=currentPage;
    this.selectHoldMoneyManagementInfo();
  }
}
