import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { ContractSigningBean } from './bean/contract-signing.bean';

//贷款明细信息
@Component({
  selector: 'contract-signing',
  templateUrl: './contract-signing.component.html',
  styleUrls: ['./contract-signing.component.scss'],
  providers: [Commfunc]
})
export class ContractSigningComponent implements OnInit {

  @Input() inValue: any;
  msgs: Message[] = [];//提示信息
  contractSigningBean: ContractSigningBean = new ContractSigningBean();
  display: boolean = false;
  subsidiaryDisplay: boolean = false;//积分明细
  periodDisplay: boolean = false;
  title: string;
  code: any;
  cunKuanMethods: any[];
  deductions: any[];
  depositSumTypeOptions: any = [];//存款累计方式
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) {
    this.codeVal();
    this.depositSumTypeOptions = this.code['depositSumTypeBigCus'];//存款累计方式
    this.cunKuanMethods = this.code['accumlativeDeposit']
    this.deductions = this.code.deductionMode;
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnInit() {
    this.cardDetailQuery();
  }

  //初始化
  preset() {

  }

  //模态框
  showDialog(i) {
    console.log(this.inValue.cardNo);
    if (i == 1) {
      this.router.navigate(['/pages/tzb/custom/custom-information/mine-custom/integral-info-query/customer-integral-query', { ss: this.contractSigningBean.cardNo, aa: this.contractSigningBean.custName, cc:this.contractSigningBean.custNo}]);
    } else {
      this.router.navigate(['/pages/tzb/custom/custom-information/mine-custom/integral-info-query/integral-date-query', { ss: this.contractSigningBean.cardNo, aa: this.contractSigningBean.custName, cc:this.contractSigningBean.custNo }]);
    }
  }

  //卡明细
  cardDetailQuery() {
    let param = {
      custNo: this.inValue.custId,
      pageNum: 1,
      pageSize: 10
    }
    this.httpService.cardDetailQuery(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.contractSigningBean = data.resultList[0];
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

}
