import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CreditCardDetailsBean } from './bean/credit-card-details.bean';
import { tableMessage, CALENDAR_CN, yearRangeBig, yearRangeSmall } from 'app/@uisftech/common/constant/config';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import { DomSanitizer } from '@angular/platform-browser';
import { TZB_CREDIT_CARD, TZB_TABLE_DEPOSIT_LOAN } from 'app/@uisftech/common/service/http.constant';
import * as $ from 'jquery';
@Component({
    selector: 'creditcard-details',
    templateUrl: './creditcard-details.component.html',
    styleUrls: ['./creditcard-details.component.scss'],
    providers: [Commfunc, CusViewHttpService, DatePipe]
})
export class CreditCardDetailsComponent implements OnInit, OnChanges {
    @Input() inValue: any;
    @Output() outValue = new EventEmitter();
    code: any;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    ch: any = CALENDAR_CN;//日期
    maxDate: any = null;
    minDate: any = null;
    msgs: Message[];
    first: number = 0;
    //总页数
    total: string;
    dataTable: any = [];
    startDate: any;
    endDate: any;
    showHideStart: boolean = false;
    showHideEnd: boolean = false;
    yearRangeSmall: any = yearRangeSmall;
    yearRangeBig: any = yearRangeBig;
    yearRange: any;
    creditCardDetailsBean: CreditCardDetailsBean = new CreditCardDetailsBean();
    user: any;
    orgId: string = '';
    userId: string = '';
    targetId: any;
    srcUrl_1;
    params;
    constructor(
        public httpService: CusViewHttpService,
        public commfunc: Commfunc,
        public router: Router,
        public datePipe: DatePipe,
        private sanitizer: DomSanitizer
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user.targetId) {
            this.targetId = this.user.targetId;
        }
    }
    ngOnInit() {



        this.yearRange = `${yearRangeSmall}:${yearRangeBig}`;
        this.creditCardDetailsBean.pageNum = 1;
        this.creditCardDetailsBean.pageSize = 10;

        let s = new Date();
        this.endDate = s;
        let one_day = 24 * 60 * 60 * 1000;
        // let m = `${s.getFullYear() - 1}-${s.getMonth() + 1}-${s.getDate() + 1}`;
        let ddate = moment().add(-3, 'month').format('Y-MM-DD');
        let three_before = new Date(ddate).getTime() + one_day;
        this.startDate = new Date(three_before);
        
        let temp = {
            commonHeader: this.user,
            cardNo: this.inValue.cardNum,
            startDate: this.startDate,
            endDate: this.endDate
        }
        console.log(temp + '   qqqq' + this.startDate)
        this.params = encodeURIComponent(JSON.stringify(temp));
        let depositUrl = TZB_CREDIT_CARD + "?params=" + this.params + "&&serviceId=CeCardTradeDetailStaticPages";
        this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(depositUrl);
    }
    ngOnChanges() {
        this.creditCardDetailsBean.cardNo = this.inValue.cardNum;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id);
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //翻页
    paginate(event) {
        //每页显示的条数
        this.creditCardDetailsBean.pageSize = event.rows * 1;
        //当前页
        this.creditCardDetailsBean.pageNum = event.page + 1;
        this.first = event.page * event.rows;
        this.query();
    }
    queryFirst() {
        this.creditCardDetailsBean.pageNum = 1;
        this.creditCardDetailsBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    query() {
        let temp = {
            commonHeader: this.user,
            cardNo: this.inValue.cardNum,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.params = encodeURIComponent(JSON.stringify(temp));
        let depositUrl = TZB_CREDIT_CARD + "?params=" + this.params + "&&serviceId=CeCardTradeDetailStaticPages";
        this.srcUrl_1 = this.sanitizer.bypassSecurityTrustResourceUrl(depositUrl);
    }
    //时间
    chectStart(event) {
        let str = new Date(this.startDate);
        let year_later = str.getFullYear() * 1 + 1;
        let one_year;
        let one_day = 24 * 60 * 60 * 1000;
        if (year_later / 4 == 0 && (year_later / 100 != 0 || year_later / 400 == 0)) {
            one_year = 366 * 24 * 60 * 60 * 1000;
        } else {
            one_year = 365 * 24 * 60 * 60 * 1000;
        }
        if (event && event != null && event != '') {
            let end_Time = str.getTime() + one_year - one_day;
            let today_time = new Date().getTime();
            if (end_Time > today_time) {
                this.endDate = new Date();
            } else {
                this.endDate = new Date(end_Time)
            }
            if (str.getTime() > today_time) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于当前日期' }];
                this.startDate = new Date(today_time);
                return;
            }
        }
    }
    chectStart1(event) {
        if (this.startDate && this.startDate != null && this.startDate != '' && this.endDate && this.endDate != '' && this.endDate != null) {
            let str = new Date(this.startDate);
            let year_later = str.getFullYear() * 1 + 1;
            let one_year;
            let one_day = 24 * 60 * 60 * 1000;
            if (year_later / 4 == 0 && (year_later / 100 != 0 || year_later / 400 == 0)) {
                one_year = 366 * 24 * 60 * 60 * 1000;
            } else {
                one_year = 365 * 24 * 60 * 60 * 1000;
            }
            let end_Time = str.getTime() + one_year - one_day;
            let today_time = new Date().getTime();
            if (end_Time > today_time) {
                this.endDate = new Date();
            } else {
                this.endDate = new Date(end_Time)
            }
            if (str.getTime() > this.endDate.getTime()) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' }];
                this.showHideStart = !this.showHideStart;
                event = '';
                this.startDate = event;
                return;
            }
            if ((this.endDate.getTime() - str.getTime()) >= one_year) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询结束时间与起始日期间隔不能超过一年' }];
                this.showHideStart = !this.showHideStart;
                event = '';
                this.startDate = event;
                return;
            }
            if (str.getTime() > today_time) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于当前日期' }];
                this.startDate = new Date(today_time);
                return;
            }
        }
    }
    chectEnd(event) {
        if (event && event != null && event != '' && this.startDate && this.startDate != '' && this.startDate != null) {
            let str = new Date(this.startDate);
            let year_later = str.getFullYear() * 1 + 1;
            let one_year;
            let one_day = 24 * 60 * 60 * 1000;
            if (year_later / 4 == 0 && (year_later / 100 != 0 || year_later / 400 == 0)) {
                one_year = 366 * 24 * 60 * 60 * 1000;
            } else {
                one_year = 365 * 24 * 60 * 60 * 1000;
            }
            let this_time = new Date();
            if (this.endDate.getTime() > this_time.getTime()) {
                this.endDate = this_time;
            }
            if (str.getTime() > event.getTime()) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' }];
                this.showHideStart = !this.showHideStart;
                event = '';
                this.endDate = event;
                return;
            }
            if ((event.getTime() - str.getTime()) >= one_year) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询结束时间与起始日期间隔不能超过一年' }];
                this.showHideStart = !this.showHideStart;
                event = '';
                this.endDate = event;
                return;
            }
        }
    }
    chectEnd1(event) {
        if (this.endDate && this.endDate != null && this.endDate != '' && this.startDate && this.startDate != '' && this.startDate != null) {
            let str = new Date(this.startDate);
            let year_later = str.getFullYear() * 1 + 1;
            let one_year;
            let one_day = 24 * 60 * 60 * 1000;
            if (year_later / 4 == 0 && (year_later / 100 != 0 || year_later / 400 == 0)) {
                one_year = 366 * 24 * 60 * 60 * 1000;
            } else {
                one_year = 365 * 24 * 60 * 60 * 1000;
            }
            let this_time = new Date();
            if (this.endDate.getTime() > this_time.getTime()) {
                this.endDate = this_time;
            }
            if (str.getTime() > this.endDate.getTime()) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询起始时间不能大于结束日期' }];
                this.showHideStart = !this.showHideStart;
                event = '';
                this.endDate = event;
                return;
            }
            if ((this.endDate.getTime() - str.getTime()) >= one_year) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '查询结束时间与起始日期间隔不能超过一年' }];
                this.showHideStart = !this.showHideStart;
                event = '';
                this.endDate = event;
                return;
            }
        }
    }
}