import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';
import {CusViewHttpService} from '../../../../http/custom-view/custom-view.http.service'; 
import {Commfunc} from 'app/@uisftech/common/service/commonFun'; 
import { tableMessage } from 'app/@uisftech/common/constant/config';
// import {HeTongZhuangTai,TenClass} from '../../../../constant/custView.codeValue';
//承兑详情
@Component({
  selector: 'deposit-details',
  templateUrl: './deposit-details.component.html',
  styleUrls: ['./deposit-details.component.scss']
})
export class DepositDetailsComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue = new EventEmitter();
  pageNum: number = 1;
  pageSize: number = 1000;
  custNo:string;
  // 承兑明细分页
  holdDepositInfoFirst:number = 0;
  holdDepositInfototalNum:number = 0;
  holdDepositInfopageSize:number = 10;
  holdDepositInfopageNum:number = 1;
  totalBalance: string;//存款总余额
  totalAvailableCardinality: string;//存款总（本年）可用积数
  totalYearAdditionalCardinality: string;//本年总附加积数
  totalContributionCardinalNumber: string;//贡献积数
  holdDepositInfo: any[];//存款
  tabMesg:any = tableMessage;//表格中无数据时显示的内容
  heTongZhuangTaiOptions:any=[];//合同状态
  tenClassOptions:any=[];//十级分类
  code:any;
  constructor(
    public httpService:CusViewHttpService,
    public commfunc:Commfunc
  ) {
    this.codeVal();
    this.heTongZhuangTaiOptions=this.code['HeTongZhuangTai'];
    this.tenClassOptions=this.code['TenClass'];
   }

  ngOnInit() {
    if(this.inValue.appUserId&&this.inValue.appUserId!=''&&this.inValue.appUserId!=null){
      this.httpService.queryTellerById({ tellerId: this.inValue.appUserId}).subscribe(data => {
        if(data.tellerName&&data.tellerName!=''&&data.tellerName!=null){
          this.inValue.appUserId = data.tellerName;              
        }
      });
    }
    this.selectHoldDepositInfo();
  }
  selectHoldDepositInfo(){
    let param={
      contractNo: this.inValue.contractNo,
      pageNum: this.holdDepositInfopageNum,
      pageSize: this.holdDepositInfopageSize,
      // contractNo: '0117005708',      
    }
    let param1={
      contractNo: this.inValue.contractNo,
      clientNo: this.inValue.clientNo,
      tranType: '0',
      billCategory: '1',//票据种类，信贷系统没有用到该条件
      pageNum: this.holdDepositInfopageNum,
      pageSize: this.holdDepositInfopageSize,
    }
    //如果合同状态是未执行（01）
    if(this.inValue.contractStatus =='01'){
      this.httpService.BZ_0300300004415_LOAN(param).subscribe(data=>{
        // this.holdDepositInfopageNum=data.pageNum;
        // this.holdDepositInfopageSize=data.pageSize;
        this.holdDepositInfototalNum=data.totalNum;
        this.holdDepositInfo = data.infoList == null ? [] : data.infoList;
      });
    }else{
      this.httpService.BZ_0200300000301_LOAN(param1).subscribe(data=>{
        if(data.returnCode.code=='success'){
          // this.holdDepositInfopageNum=data.pageNum;
          // this.holdDepositInfopageSize=data.pageSize;
          this.holdDepositInfototalNum=data.totalNum;
          data.infoList.forEach(item =>{
            item['draftNo']=item.billNo;
            item['billBankId']=item.billBankName;
            item['billSignDate']=item.billDate;
            item['matureDate']=item.billPayDate;
          });
          this.holdDepositInfo = data.infoList == null ? [] : data.infoList;
        }
      });
    }
  }

  paginate(event){
    // 分页
    this.holdDepositInfopageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.holdDepositInfopageNum = currentPage;
    this.holdDepositInfoFirst = (currentPage - 1) * this.holdDepositInfopageSize;
    this.selectHoldDepositInfo();
  }
//码值
codeVal() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
}
}
