import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ViewProductInformationComponent } from "./view-product-information.component";
import { OwnOurProductComponent } from "./own-our-product/own-our-product.component";
import { ApplicationForProductComponent } from "./application-for-product/application-for-product.component";
import { OtherBankingProductComponent } from "./other-banking-product/other-banking-product.component";
import { RecommendedProductComponent } from "./recommended-product/recommended-product.component";

export const routes: Routes = [
    {
        path: '',component: ViewProductInformationComponent,
        children: [
            {
                path: '', redirectTo: 'own-our-product', pathMatch: 'full'
            },
            {
                path: 'own-our-product',component: OwnOurProductComponent,
            },
            {
                path: 'appl-for-product', component: OwnOurProductComponent
            },
            {
                path: 'other-bank-product', component: OwnOurProductComponent
            },
            {
                path: 'recommended-product', component: OwnOurProductComponent
            }
        ]
    }];
export const ViewProductInformationRouting: ModuleWithProviders = RouterModule.forChild(routes);