import { Component, OnInit, OnChanges } from '@angular/core';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { Observable } from 'rxjs/Rx';
import {Commfunc} from 'app/@uisftech/common/service/commonFun';
import {ApplayForProductBean} from '../../bean/custom-view.bean';
import {Message} from 'primeng/primeng';
// import { TenClass,ContractMode,AppStep,ApproveStatuss,BussType,SubBussType,GuaranteeMode,TranLoanFlag,EffLoanFlag,PayType,CreditType,LoanPurpuseCode,HandleResult,SubVetoType} from '../../../constant/custView.codeValue';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-application-for-product',
  templateUrl: './application-for-product.component.html',
  styleUrls: ['./application-for-product.component.scss']
})
export class ApplicationForProductComponent implements OnInit,OnChanges {
  items:any[];
  display:boolean=false;
  custNo:any;
  appUserId:any;
  pageSize:number=10;
  pageNum:number=1;
  totalNum:number=0;
  first:number=0;
  applayForProductBean:ApplayForProductBean=new ApplayForProductBean();
  msgs:any[]=[];
  guaranteeModeOptions:any[]=[];//担保方式
  tabMesg:any = tableMessage;//表格中无数据时显示的内容
  bussTypeOptions:any[]=[];//业务类型
  subBussTypeOptions:any[]=[];//子业务类型
  tranLoanFlagOptions:any[]=[];//转化贷款标志
  effLoanFlagOptions:any[]=[];//节能增效贷款
  payTypeOptions:any[]=[];//支付类型
  creditTypeOptions:any[]=[];//信用类型
  loanPurpuseCodeOptions:any[]=[];//贷款用途代码
  handleResultOptions:any[]=[];//处理结果
  subVetoTypeOptions:any[]=[];//二级否决类型
  approveStatusOptions:any[]=[];//审批状态
  appStepOptions:any[]=[];//审批阶段
  contractModeOptions:any[]=[];//合同方式
  tenClassOptions:any[]=[];//手工十级分类
  code:any;
  constructor(
    public router: Router,
    public httpService: CusViewHttpService,
    public route: ActivatedRoute,
    public commfunc:Commfunc
  ) { 
    this.codeVal();
    this.guaranteeModeOptions=this.code['GuaranteeMode'];
    this.bussTypeOptions=this.code['BussType'];
    this.subBussTypeOptions=this.code['SubBussType'];
    this.tranLoanFlagOptions=this.code['TranLoanFlag'];
    this.effLoanFlagOptions=this.code['EffLoanFlag'];
    this.payTypeOptions=this.code['PayType'];
    this.creditTypeOptions=this.code['CreditType'];
    this.loanPurpuseCodeOptions=this.code['LoanPurpuseCode'];
    this.handleResultOptions=this.code['HandleResult'];
    this.subVetoTypeOptions=this.code['SubVetoType'];
    this.approveStatusOptions=this.code['ApproveStatuss'];
    this.appStepOptions=this.code['AppStep'];
    this.contractModeOptions=this.code['ContractMode'];
    this.tenClassOptions=this.code['TenClass'];
    //-------------------------------------------------------------------上面是码值
    this.custNo=this.commfunc.getSessionData('custNo');
    if(JSON.parse(this.commfunc.getSessionData('commonHeader'))){
      this.appUserId=JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;         
    }
  }

  ngOnInit() {
    let param={
      appUserId:this.appUserId,
      clientNo:this.custNo,
      // appUserId:'0500',
      // clientNo:'1000000036',
      pageNum:this.pageNum,
      pageSize:this.pageSize
    };
    this.httpService.BZ_0200300002001_LOAN(param).subscribe(data=>{
      if(data.returnCode.code=='success'){
        data.infoList.forEach(item => {
          this.httpService.queryOrgById({orgId:item.appBranchNo}).subscribe(data=>{
            if(data.orgName!=null){
              item.appBranchNo=data.orgName;              
            }
          });
        });
        this.items=data.infoList;
        this.pageSize=data.pageSize;
        this.pageNum=data.pageNum;
        this.totalNum=data.totalNum;
      }
    })

  }
  ngOnChanges(){
   
  }
  showDialog(param){
    let params={
      consumerSeqNo:param.tellSeqNo,
      appUserId:this.appUserId,
      // consumerSeqNo:'SN000000000001196302',
      // appUserId:this.appUserId,
    }
    this.httpService.BZ_0200300002002_LOAN(params).subscribe(data=>{
      if(data.returnCode.code=='success'){
        this.applayForProductBean=data;
        this.httpService.queryTaskByIndtstryAll({taskTypeId: this.applayForProductBean.investIndustry, taskTypeKey: 'indtstryType'}).subscribe(data=>{
          if(data.returnCode.code=='success'){
            this.applayForProductBean.investIndustry=data.taskList[0].taskTypeName;             
          }
        });
        this.httpService.queryTellerById({ tellerId: this.applayForProductBean.investigatorName }).subscribe(data => {
          this.applayForProductBean.investigatorName = data.tellerName;
        });
        this.httpService.queryTellerById({ tellerId: this.applayForProductBean.investigatorName2 }).subscribe(data => {
          this.applayForProductBean.investigatorName2 = data.tellerName;
        });
        this.httpService.queryOrgById({orgId:this.applayForProductBean.appBranchNo}).subscribe(data=>{
          if(data.orgName!=null){
            this.applayForProductBean.appBranchNo=data.orgName;            
          }
        });
      }else{
        data.returnCode.message?data.returnCode.message:'调用服务失败';
        this.msgs=[{severity:'error', summary:'提示', detail:data.returnCode.message}];
      }
    })
    this.display=true;
    document.body.style.overflow='hidden';    
  }
  close(){
    this.display=false;
    document.body.style.overflow='auto';    
  }
  paginate(event){
    this.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    let param={
      appUserId:this.appUserId,
      clientNo:this.custNo,
      pageNum:this.pageNum,
      pageSize:this.pageSize
    };
    this.httpService.BZ_0200300002001_LOAN(param).subscribe(data=>{
      if(data.returnCode.code=='success'){
        data.infoList.forEach(item => {
          this.httpService.queryOrgById({orgId:item.appBranchNo}).subscribe(data=>{
            if(data.orgName!=null){
              item.appBranchNo=data.orgName;              
            }
          });
        });
        this.items=data.infoList;
        this.pageSize=data.pageSize;
        this.pageNum=data.pageNum;
        this.totalNum=data.totalNum;
      }
    })
  }
    //权限
    judgeField(fId) {
      
          let ret = this.commfunc.permissionFiledCheck(fId);
          return ret
          // return true
        }
        toOwnOurProduct() {
          this.router.navigate(['pages/custom-view/view-product-information/own-our-product'])
        }
        toApplForProduct() {
          this.router.navigate(['pages/custom-view/view-product-information/appl-for-product'])
        }
        toOtherBankProduct() {
          this.router.navigate(['pages/custom-view/view-product-information/other-bank-product'])
        }
        toRecommendedProduct() {
          this.router.navigate(['pages/custom-view/view-product-information/recommended-product'])
        }
      
        tock() {
          if (document.querySelector('#ck') != null) {
            document.querySelector('#ck').scrollIntoView();
            let s=window.scrollY;
            window.scrollTo(100,s-50);
          }
        }
        toDk() {
          if (document.querySelector('#dk') != null) {
            document.querySelector('#dk').scrollIntoView();
            let s=window.scrollY;
            window.scrollTo(100,s-50);
          }
        }
        toLc() {
          if (document.querySelector('#lc') != null) {
            document.querySelector('#lc').scrollIntoView();
            let s=window.scrollY;
            window.scrollTo(100,s-50);
          }
        }
        toOtherQy() {
          if (document.querySelector('#otherqy') != null) {
            document.querySelector('#otherqy').scrollIntoView();
            let s=window.scrollY;
            window.scrollTo(100,s-50);
          }
        }
        toDj() {
          if (document.querySelector('#dj') != null) {
            document.querySelector('#dj').scrollIntoView();
            let s=window.scrollY;
            window.scrollTo(100,s-50);
          }
        }
        toMy() {
          if (document.querySelector('#my') != null) {
            document.querySelector('#my').scrollIntoView();
            let s=window.scrollY;
            window.scrollTo(100,s-50);
          }
        }
        //码值
codeVal() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
}
}
